
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

export default {
name: "shippingApplication",
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Swiper,
SwiperItem,
Cell,
Group,
XInput,
PopupPicker,
XButton,
Box,
XAddress,
ChinaAddressV4Data,
Checker,
CheckerItem
},
created() {
this.queryHistoryShipOrder();
this.queryShipOrder();
},
computed: {
},
data() {
return {
index: 0,
checkerWhich: 2,
userInfo: {},
orderList: [],
shipOrderList: []
};
},
methods: {
queryHistoryShipOrder() {
this.$api
.queryHistoryShipOrder({
pageNo: 1,
pageSize: 1000
})
.then(data => {
if (data.code === 200) {
this.orderList = data.data.items;
}
});
},
queryShipOrder() {
this.$api
.queryShipOrder({
pageNo: 1,
pageSize: 1000
})
.then(data => {
if (data.code === 200) {
this.shipOrderList = data.data.items;
}
});
},
cancelOrder(id) {
this.$api
.cancelOrder({
id: id
})
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("取消订单成功", "top");
this.queryOrder();
}
});
},
toSale() {
this.$router.push({
name: "sales"
});
},
selectChecker(val) {
console.log(val);


}
}
};



